import React from "react";
import { Link } from "react-router-dom";
import { Button, ButtonGroup, Col, Container, Row } from "reactstrap";
import PublisherAPI, { PublisherAPIResult } from "../../../../api/admin/publisher-api";
import WForm from "../../../../components/wform";
import WInput from "../../../../components/winput";
import AMRWindow from "../../../../page-component/admin/admin-message-request-disable-window";

const goBackURL = "/admin/book/publisher/view"

export default class AddPublisher extends React.Component {
    amr = React.createRef<AMRWindow>();
    form = React.createRef<WForm>();
    componentDidMount() {
        const form = this.form.current
        if (!form) return
        form.setData({
            name: "Bnh nh",
            publisherCode: "BINH_DINH"
        } as PublisherAPIResult)
    }
    insert(evt: any) {
        evt.preventDefault()
        const { form: { current: form }, amr: { current: amr } } = this
        if (!form || !amr) return
        const sendRequest = amr.getMessageRequest().sendRequest

        const data: PublisherAPIResult = form.getData()
        sendRequest(PublisherAPI.addPublisher(data))
    }
    render() {
        return <AMRWindow ref={this.amr} className="form-container">
            <WForm ref={this.form} onSubmit={this.insert.bind(this)} >
                <h3>Thm nh xut bn</h3>
                <Container fluid={true} className="p-0">
                    <Row>
                        <Col xs={12}>
                            <WInput title_input="M nh xut bn" name="publisherCode" />
                        </Col>
                        <Col xs={12}>
                            <WInput title_input="Tn nh xut bn" name="name" />
                        </Col>
                    </Row>
                    <div className="d-flex space-sm mt-2">
                        <Button size="sm" color="primary">Thm</Button>
                        <Link to={goBackURL} className="btn btn-sm btn-danger">Quay v</Link>
                    </div>
                </Container>
            </WForm>
        </AMRWindow>
    }
}