import moment from "moment";
import React from "react";
import { Link } from "react-router-dom";
import { Button } from "reactstrap";
import CategoryAPI, { CategoryAPIResult } from "../../../../api/admin/category-book-api";
import ReloadSVG from "../../../../logo-svg/reload";
import DetailAdminPage from "../../../../page-component/admin/menu-detail";
import UpdateCategory from "./update";

const goBackURL = "/admin/book/category/view"
const mainURL = "/admin/book/category/detail"

interface DetailCategoryProps {
    goBack?: {
        action: () => void,
        id: number,
    }
}
interface DetailCategoryState {
    categoryInfo: CategoryAPIResult,
    isNotFoundResult: boolean,
}
export default class DetailCategory extends React.Component<DetailCategoryProps, DetailCategoryState> {
    detailPage: React.RefObject<DetailAdminPage> = React.createRef();
    nextUpdate = false;
    goBackURL: string;

    constructor(props: any) {
        super(props);
        this.state = {
            categoryInfo: {} as any,
            isNotFoundResult: false,
        }
        this.goBackURL = window.location.href
    }
    componentDidMount() {
        this.getCategory();
    }
    getAdminMessageReq() {
        return this.detailPage.current?.adminMessageRequest.current as any;
    }
    render() {
        const { categoryInfo } = this.state;
        const goBackAction = this.props.goBack
            ? () => {
                window.history.pushState('', '', this.goBackURL)
                this.props.goBack?.action();
            }
            : goBackURL

        const viewBook = (<div>
            <div className="d-flex justify-content-between">
                <h3><strong>{this.state.categoryInfo.category}</strong></h3>
            </div>
            <br />
            <table className="table wtable-info">
                <tbody>
                    <tr><td>ID</td><td>{categoryInfo.id}</td></tr>
                    <tr><td>M th loi</td><td>{categoryInfo.categoryCode}</td></tr>
                    <tr><td>Tn th loi</td><td>{categoryInfo.category}</td></tr>
                    <tr>
                        <td>Cp nht</td>
                        <td><Link to={`/admin/admin-user/detail?adminCode=${categoryInfo.modifiedBy}`}>{categoryInfo.modifiedBy}</Link> - {moment(categoryInfo.modifiedDate).calendar()}</td>
                    </tr>
                    <tr>
                        <td>To bi</td>
                        <td><Link to={`/admin/admin-user/detail?adminCode=${categoryInfo.createBy}`}>{categoryInfo.createBy}</Link> - {moment(categoryInfo.createDate).calendar()}</td>
                    </tr>
                </tbody>
            </table>
        </div>)
        return <DetailAdminPage ref={this.detailPage}
            interceptor={(menu) => !this.state.isNotFoundResult
                ? menu
                : (<div className="d-flex space-sm">
                    <Button color="primary" size="sm" onClick={this.getCategory.bind(this)}><ReloadSVG color="white" className="icon" /> Ti li</Button>
                </div>)
            }
            goBackAction={goBackAction}
            menuConfig={[
                {
                    name: 'Xem thng tin',
                    body: viewBook,
                    callback: () => {
                        if (this.nextUpdate) {
                            this.nextUpdate = false;
                            this.getCategory();
                        }
                    }
                },
                {
                    name: 'Cp nht',
                    body: <UpdateCategory
                        categoryInfo={this.state.categoryInfo}
                        getAdminMessageRequest={this.getAdminMessageReq.bind(this)}
                        onUpdateSuccess={() => this.nextUpdate = true}
                    />
                }
            ]}
        />
    }
    async getCategory() {
        const goBackId = this.props.goBack && this.props.goBack.id
        const requestId = new URLSearchParams(window.location.search).get("id");
        const id = goBackId || requestId;
        const adminMessageRequest = this.getAdminMessageReq();

        if (id) {
            if (this.props.goBack) window.history.pushState('', '', `${mainURL}?id=${id}`)

            const resultReq = await adminMessageRequest.sendRequest(() => {
                return CategoryAPI.getCategory(id as number)
            }, { hideWhenDone: true });

            if (resultReq) {
                this.setState({ categoryInfo: resultReq.data });
                return;
            }
        }
        this.setState({ isNotFoundResult: true });
    }
}