import React from "react";
import Utils from "../../common/Utils";
import AdminMessageRequest from "./admin-message-request";

interface AMRWindowState {
    isDisable: boolean
}
export default class AMRWindow extends React.Component<React.HTMLAttributes<HTMLElement>, AMRWindowState> {
    private adminMessage = React.createRef<AdminMessageRequest>()
    constructor(props: React.HTMLAttributes<HTMLElement>) {
        super(props)
        this.state = {
            isDisable: false
        }
        Utils.preventSetStateWhenComponentUnmount(this)
        this.getMessageRequest = this.getMessageRequest.bind(this)
    }
    getMessageRequest(): AdminMessageRequest {
        return this.adminMessage.current as AdminMessageRequest;
    }
    render() {
        return <div {...this.props}>
            <AdminMessageRequest
                ref={this.adminMessage}
                onBeforeSendRequest={() => this.setState({ isDisable: true })}
                onCompleteSendRequest={() => this.setState({ isDisable: false })}
            />
            <div className={this.state.isDisable ? 'disabled' : ''}>
                {this.props.children}
            </div>
        </div>
    }
}