import React from "react";
import { Button, Col, Container, Row } from "reactstrap";
import PublisherAPI, { PublisherAPIResult } from "../../../../api/admin/publisher-api";
import WForm from "../../../../components/wform";
import WInput from "../../../../components/winput";
import AdminMessageRequest from "../../../../page-component/admin/admin-message-request";

interface UpdatePublisherProps {
    categoryInfo: PublisherAPIResult
    getAdminMessageRequest: () => AdminMessageRequest
    onUpdateSuccess: () => void
}

export default class UpdatePublisher extends React.Component<UpdatePublisherProps> {
    form = React.createRef<WForm>();

    async componentDidMount() {
        const { categoryInfo } = this.props
        this.form.current?.setData(categoryInfo)
    }
    update(evt: any) {
        evt.preventDefault();
        const {
            props: { getAdminMessageRequest, onUpdateSuccess },
            form: { current: form }
        } = this

        const amr = getAdminMessageRequest()
        if (!form || !amr) return

        const data: PublisherAPIResult = form.getData();
        const resultUpdate = amr.sendRequest(PublisherAPI.updatePublisher(data))

        if (!resultUpdate) return
        onUpdateSuccess && onUpdateSuccess()

    }
    render() {
        const { categoryInfo } = this.props
        return (
            <WForm ref={this.form as any} onSubmit={this.update.bind(this)} className="form-container">
                <Container fluid={true} className="p-0">
                    <Row>
                        <Col xs={12}>
                            <div className="d-flex justify-content-between">
                                <h3><strong>{categoryInfo.publisherCode}</strong></h3>
                            </div>
                        </Col>
                        <input type="hidden" name="id" readOnly={true} value={categoryInfo.id}></input>
                        <Col xs={12}>
                            <WInput title_input="Tn nh xut bn" name="name" />
                        </Col>
                    </Row>
                    <div className="d-flex space-nm mb-2">
                        <Button size="sm" color="primary">Cp nht</Button>
                    </div>
                </Container>
            </WForm>
        )
    }
}