import React, { useRef, useState } from "react";
import { Fragment } from "react";
import { Link } from "react-router-dom";
import { Button, Col, Container, Row } from "reactstrap";
import AuthorAPI, { AuthorAPIResult } from "../../../api/admin/author-api";
import BookAPI, { BookAPIResult } from "../../../api/admin/book-api";
import CategoryAPI, { CategoryAPIResult } from "../../../api/admin/category-book-api";
import PublisherAPI, { PublisherAPIResult } from "../../../api/admin/publisher-api";
import Utils from "../../../common/Utils";
import ListSelect from "../../../components/list-select";
import WForm from "../../../components/wform";
import WInput, { WInputOther } from "../../../components/winput";
import { AdminOverrideWindow } from "../../../contexts/admin-overide-window";
import AdminMessageRequest from "../../../page-component/admin/admin-message-request";
import AMRWindow from "../../../page-component/admin/admin-message-request-disable-window";

interface AddBookState {
    isDisable: boolean,
    publishers: PublisherAPIResult[],
    categories: any,
    authors: any,
}

export interface ModelObjectList<K> {
    [modelId: number]: K
}

export default class AddBook extends React.Component<{}, AddBookState> {
    form = React.createRef<WForm>();
    amrWindow = React.createRef<AMRWindow>();
    isShowCategories = false;
    imageUpload: File | undefined = undefined;

    constructor(props: any) {
        super(props);
        this.state = {
            isDisable: false,
            publishers: [],
            categories: {},
            authors: {}
        }
    }
    componentDidMount() {
        PublisherAPI.getAll({ unlimited: true }).then((resp) => {
            this.setState({ publishers: resp.data });
        })
        this.form.current?.setData({
            title: 'Ti thy b hng xm',
            isbn: 12,
            pageNumber: 12,
            price: 12,
            description: 'Hello',
            edition: 14
        } as BookAPIResult)
    }
    async onSubmit(evt: any) {
        evt.preventDefault();
        const {
            amrWindow: { current: amr },
            form: { current: form },
            imageUpload: file
        } = this
        if (!amr || !form) return
        const messageReq = amr.getMessageRequest()

        //upload book
        const book = { ...form.getData(), categories: Object.values(this.state.categories), authors: Object.values(this.state.authors) }
        book.publisher = { id: book.publisher };
        const resultAddBook = await messageReq.sendRequest(() => BookAPI.addBook(book));
        if (!resultAddBook) return

        //uploadImage
        const message = { ...messageReq.state }
        const bookId = resultAddBook.data.id as number

        if (!file) return
        const resultUploadImage = await messageReq.sendRequest(() => BookAPI.uploadImage({
            image: file, id: bookId
        }))

        if (!resultUploadImage) {
            message.color = 'warning'
            message.message = `${messageReq.state.message} - ${message.message}`
        }
        messageReq.setState(message)
    }
    render() {
        return <AMRWindow className="form-container" ref={this.amrWindow}>
            <WForm className={this.state.isDisable ? 'disabled' : ''} onSubmit={this.onSubmit.bind(this)} ref={this.form as any}>
                <Container fluid={true} className="p-0">
                    <Row>
                        <Col xs={12}>
                            <div className="d-flex justify-content-between">
                                <h2>Thm Sch</h2>
                                <BookCoverImage onImageChange={(file) => this.imageUpload = file} />
                            </div>
                        </Col>
                        <Col xs={12}>
                            <WInput name="title" title_input="Tn sch" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput title_input="isbn" name="isbn" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput name="pageNumber" title_input="S trang" type="number" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput name="edition" title_input="Ti bn" type="number" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput name="price" title_input="Gi" type="number" required />
                        </Col>
                        <Col xs={12}>
                            <WInputOther title_input="Nh xut bn">
                                <select name="publisher">
                                    {this.state.publishers.map((publisher, index) => {
                                        return <option key={index} value={publisher.id}>{publisher.name}</option>
                                    })}
                                </select>
                            </WInputOther>
                        </Col>
                        <Col xs={12}>
                            <AuthorsInputListSelect
                                onComplete={authors => this.setState({ authors: authors })}
                            />
                        </Col>
                        <Col xs={12}>
                            <CategoriesInputListSelect
                                onComplete={(categories) => this.setState({ categories: categories })} />
                        </Col>
                        <Col xs={12}>
                            <WInputOther title_input="M t">
                                <textarea name="description" rows={4} required></textarea>
                            </WInputOther>
                        </Col>
                        <Col>
                            <div className="d-flex space-sm">
                                <Button color="primary" size="sm">Thm Sch</Button>
                                <Link to="/admin/book/view" className="btn btn-danger btn-sm">Quay v</Link>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </WForm>
        </AMRWindow>
    }
}


export function CategoriesInputListSelect<MODEL_LIST_OBJ extends ModelObjectList<API_RESULT>, API_RESULT extends CategoryAPIResult>({ onComplete, valuesSelect }: {
    onComplete: (categories: MODEL_LIST_OBJ) => any,
    valuesSelect?: MODEL_LIST_OBJ
}) {
    if (typeof valuesSelect !== 'object') valuesSelect = {} as MODEL_LIST_OBJ
    const [categories, setCategories] = useState(valuesSelect)
    return (
        < div className="d-flex space-nm align-items-end" >
            <WInput title_input="Th loi" readOnly required is_invalid={Object.keys(categories).length === 0}
                value={Object.values(categories)
                    .map(category => category.category).join(', ')} />
            <AdminOverrideWindow.Consumer>
                {({ addWindow, removeWindow }) => {
                    return <Button color="primary" size="sm"
                        className="mb-2"
                        style={{ whiteSpace: 'nowrap' }}
                        onClick={() => {
                            const categoriesView = addWindow(
                                <div className="default-view">
                                    <Link to="/admin/category/add" className='btn btn-primary btn-sm' children='Thm th loi' />
                                    <ListSelect
                                        getData={async () => (await CategoryAPI.getAllCategory({ unlimited: true })).data}
                                        viewData={(data: API_RESULT) => {
                                            return <Fragment>
                                                <td key="categoryCode">{data.categoryCode}</td>
                                                <td key="category">{data.category}</td>
                                            </Fragment>
                                        }}
                                        typeSearch={[
                                            { propertySearch: 'categoryCode', viewSearch: 'M th loi' },
                                            { propertySearch: 'category', viewSearch: 'Th loi' }
                                        ]}
                                        header={<Fragment>
                                            <th key="categoryCode">M th loi</th>
                                            <th key="category">Th loi</th>
                                        </Fragment>}
                                        getKey={(data: API_RESULT) => { return data.id }}
                                        onComplete={(categories) => {
                                            removeWindow(categoriesView as any);
                                            setCategories(categories)
                                            onComplete(categories)
                                        }}
                                        valueSelected={categories}
                                    />
                                </div>)
                        }} >Th loi</Button>
                }}
            </AdminOverrideWindow.Consumer>
        </ div>
    )
}

export function AuthorsInputListSelect<MODEL_LIST_OBJ extends ModelObjectList<API_RESULT>, API_RESULT extends AuthorAPIResult>({ onComplete, valuesSelect }: {
    onComplete: (authors: MODEL_LIST_OBJ) => any,
    valuesSelect?: MODEL_LIST_OBJ
}) {
    if (!valuesSelect || typeof valuesSelect !== 'object') valuesSelect = {} as MODEL_LIST_OBJ
    const [authors, setAuthors] = useState<MODEL_LIST_OBJ>(valuesSelect)
    return (
        <div className="d-flex space-nm align-items-end">
            <WInput title_input="Tc gi" readOnly required is_invalid={Object.keys(authors).length === 0}
                value={Object.values(authors)
                    .map((author) => author.name)
                    .join(', ')} />
            <AdminOverrideWindow.Consumer>
                {({ addWindow, removeWindow }) => {
                    return <Button color="primary" size="sm"
                        style={{ whiteSpace: 'nowrap' }}
                        className="mb-2"
                        onClick={() => {
                            let authorView = addWindow(
                                <div className="default-view">
                                    <Link to="/admin/author/add" className='btn btn-primary btn-sm' children='Thm tc gi' />
                                    <ListSelect
                                        getData={async () => (await AuthorAPI.getAll({ unlimited: true })).data}
                                        viewData={(data: API_RESULT) => {
                                            return <Fragment>
                                                <td>{data.authorCode}</td>
                                                <td>{data.name}</td>
                                            </Fragment>
                                        }}
                                        typeSearch={[
                                            { propertySearch: 'authorCode', viewSearch: 'M Tc gi' },
                                            { propertySearch: 'name', viewSearch: 'Tn Tc gi' }
                                        ]}
                                        header={<Fragment>
                                            <th>M tc gi</th>
                                            <th>Tn tc gi</th>
                                        </Fragment>}
                                        getKey={(data: API_RESULT) => { return data.id }}
                                        onComplete={(authors: MODEL_LIST_OBJ) => {
                                            removeWindow(authorView as any);
                                            setAuthors(authors)
                                            onComplete(authors)
                                        }}
                                        valueSelected={authors}
                                    />
                                </div>)
                        }} >Tc gi</Button>
                }}
            </AdminOverrideWindow.Consumer>
        </div>
    )
}

export function BookCoverImage({ onImageChange, imagePath = "default" }: { onImageChange?: (file: File) => void, imagePath?: string }) {
    const imageView = useRef<HTMLImageElement>(null);
    const inputImage = useRef<HTMLInputElement>(null);

    return <div>
        <img className="avatar" title="nh ba sch"
            style={{ borderRadius: 0 }}
            src={process.env.REACT_APP_BOOK_IMAGE_PATH + `${imagePath}.jpg`}
            alt="default" ref={imageView}
            onClick={() => { inputImage.current?.click() }}
        ></img>
        <input type="file" className="d-none" ref={inputImage} onChange={(evt) => {
            const imgView = imageView.current
            if (!imgView) return
            const file = Utils.viewImageFromInputFileE(evt.currentTarget, imgView) as File
            onImageChange && onImageChange(file)
        }} />
    </div >
}