import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { Button, Col, Container, Row } from "reactstrap";
import { AuthorAPIResult } from "../../../api/admin/author-api";
import BookAPI, { BookAPIResult } from "../../../api/admin/book-api";
import CategoryAPI, { CategoryAPIResult } from "../../../api/admin/category-book-api";
import PublisherAPI, { PublisherAPIResult } from "../../../api/admin/publisher-api";
import Utils from "../../../common/Utils";
import ListSelect from "../../../components/list-select";
import WForm from "../../../components/wform";
import WInput, { WInputOther } from "../../../components/winput";
import { AdminOverrideWindow } from "../../../contexts/admin-overide-window";
import AdminMessageRequest from "../../../page-component/admin/admin-message-request";
import { AuthorsInputListSelect, BookCoverImage, CategoriesInputListSelect, ModelObjectList } from "./add-book";

interface UpdateBookProps {
    bookInfo: BookAPIResult
    getAdminMessageRequest: () => AdminMessageRequest
    onUpdateSuccess: () => void
}
interface UpdateBookState {
    publishers: PublisherAPIResult[]
    categories: ModelObjectList<CategoryAPIResult>,
    authors: ModelObjectList<AuthorAPIResult>,
}

export default class UpdateBook extends React.Component<UpdateBookProps, UpdateBookState> {
    form = React.createRef<WForm>();
    imageUpload: File | undefined = undefined;

    constructor(props: UpdateBookProps) {
        super(props);
        const { bookInfo } = props
        this.state = {
            publishers: [],
            categories: Utils.modelListToObject(bookInfo.categories || []),
            authors: Utils.modelListToObject(bookInfo.authors || [])
        }
    }
    async componentDidMount() {
        const { bookInfo } = this.props
        const publishers = (await PublisherAPI.getAll({ unlimited: true })).data
        this.setState({ publishers: publishers })
        setTimeout(() => this.form.current?.setData({ ...bookInfo, publisher: bookInfo.publisher?.id }))
    }
    update(evt: any) {
        evt.preventDefault();
        const data: BookAPIResult = this.form.current?.getData();
        data.publisher = { id: data.publisher as any } as any

        const {
            state: { categories, authors },
            props: { getAdminMessageRequest, onUpdateSuccess },
            imageUpload
        } = this
        const amr = getAdminMessageRequest()

        data.categories = Object.values(categories)
        data.authors = Object.values(authors)
        const resultUpdate = amr.sendRequest(() => BookAPI.updateBook(data))

        if (!resultUpdate) return
        onUpdateSuccess && onUpdateSuccess()

        if (!imageUpload) return

        // uploadImage
        const messageUpdate = amr.state.message;

        const resultUpdateImageBookCover = amr.sendRequest(() => BookAPI.uploadImage({ id: data.id as any, image: imageUpload }))
        if (resultUpdateImageBookCover) amr.setState({ message: messageUpdate })
        else amr.setState({ message: `${messageUpdate} => ${amr.state.message}`, color: "warning" })

    }
    render() {
        const { bookInfo } = this.props
        return (
            <WForm ref={this.form as any} onSubmit={this.update.bind(this)} className="form-container">
                <Container fluid={true} className="p-0">
                    <Row>
                        <Col xs={12}>
                            <div className="d-flex justify-content-between">
                                <h3><strong>{bookInfo.title}</strong></h3>
                                <BookCoverImage onImageChange={(file) => this.imageUpload = file} />
                            </div>
                        </Col>
                        <input type="hidden" name="id" readOnly={true} value={bookInfo.id}></input>
                        <input type="hidden" name="isbn" readOnly={true} value={bookInfo.isbn}></input>
                        <Col xs={12}>
                            <WInput name="title" title_input="Tn sch" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInputOther title_input="Nh xut bn">
                                <select name="publisher">
                                    {this.state.publishers.map((publisher, index) => {
                                        return <option key={index} value={publisher.id}>{publisher.name}</option>
                                    })}
                                </select>
                            </WInputOther>
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput name="pageNumber" title_input="S trang" type="number" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput name="edition" title_input="Ti bn" type="number" required />
                        </Col>
                        <Col md={6} sm={12}>
                            <WInput name="price" title_input="Gi" type="number" required />
                        </Col>
                        <Col xs={12}>
                            <AuthorsInputListSelect
                                valuesSelect={Utils.modelListToObject(bookInfo.authors || [])}
                                onComplete={authors => this.setState({ authors: authors })}
                            />
                        </Col>
                        <Col xs={12}>
                            <CategoriesInputListSelect
                                valuesSelect={Utils.modelListToObject(bookInfo.categories || [])}
                                onComplete={categories => this.setState({ categories: categories })}
                            />
                        </Col>
                        <Col xs={12}>
                            <WInputOther title_input="M t">
                                <textarea name="description" rows={4} required></textarea>
                            </WInputOther>
                        </Col>
                        <Col>
                            <div className="d-flex space-sm">
                                <Button color="primary" size="sm">Cp nht</Button>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </WForm>
        )
    }
}