import React from "react";
import { Button, Col, Container, Row } from "reactstrap";
import AuthorAPI, { AuthorAPIResult } from "../../../../api/admin/author-api";
import WForm from "../../../../components/wform";
import WInput, { WInputOther } from "../../../../components/winput";
import AdminMessageRequest from "../../../../page-component/admin/admin-message-request";

interface UpdateAuthorProps {
    authorInfo: AuthorAPIResult
    getAdminMessageRequest: () => AdminMessageRequest
    onUpdateSuccess: () => void
}

export default class UpdatePublisher extends React.Component<UpdateAuthorProps> {
    form = React.createRef<WForm>();

    async componentDidMount() {
        const { authorInfo: categoryInfo } = this.props
        this.form.current?.setData(categoryInfo)
    }
    update(evt: any) {
        evt.preventDefault();
        const {
            props: { getAdminMessageRequest, onUpdateSuccess },
            form: { current: form }
        } = this

        const amr = getAdminMessageRequest()
        if (!form || !amr) return

        const data: AuthorAPIResult = form.getData();
        const resultUpdate = amr.sendRequest(AuthorAPI.updateAuthor(data))

        if (!resultUpdate) return
        onUpdateSuccess && onUpdateSuccess()

    }
    render() {
        const { authorInfo } = this.props
        return (
            <WForm ref={this.form as any} onSubmit={this.update.bind(this)} className="form-container">
                <Container fluid={true} className="p-0">
                    <Row>
                        <Col xs={12}>
                            <div className="d-flex justify-content-between">
                                <h3><strong>{authorInfo.authorCode}</strong></h3>
                            </div>
                        </Col>
                        <input type="hidden" name="id" readOnly={true} value={authorInfo.id}></input>
                        <Col xs={12}>
                            <WInput title_input="Tn tc gi" name="name" />
                        </Col>
                        <Col>
                            <WInputOther title_input="M t">
                                <textarea name="description"></textarea>
                            </WInputOther>
                        </Col>
                    </Row>
                    <div className="d-flex space-nm mb-2">
                        <Button size="sm" color="primary">Cp nht</Button>
                    </div>
                </Container>
            </WForm>
        )
    }
}