import moment from "moment";
import React from "react";
import { Link } from "react-router-dom";
import { Button } from "reactstrap";
import BookAPI, { BookAPIResult } from "../../../api/admin/book-api";
import ReloadSVG from "../../../logo-svg/reload";
import DetailAdminPage from "../../../page-component/admin/menu-detail";
import UpdateBook from "./update-book";
interface DetailBookProps {
    goBack?: {
        action: () => void,
        bookId: number,
    }
}
interface DetailBookState {
    bookInfo: BookAPIResult,
    isNotFoundResult: boolean,
}
export default class DetailBook extends React.Component<DetailBookProps, DetailBookState> {
    detailPage: React.RefObject<DetailAdminPage> = React.createRef();
    nextUpdate = false;
    goBackURL: string;

    constructor(props: any) {
        super(props);
        this.state = {
            bookInfo: {},
            isNotFoundResult: false,
        }
        this.goBackURL = window.location.href
    }
    componentDidMount() {
        this.getBook();
    }
    getAdminMessageReq() {
        return this.detailPage.current?.adminMessageRequest.current as any;
    }
    render() {
        const { bookInfo } = this.state;
        const goBackAction = this.props.goBack
            ? () => {
                window.history.pushState('', '', this.goBackURL)
                this.props.goBack?.action();
            }
            : "/admin/book/view"

        const viewBook = (<div>
            <div className="d-flex justify-content-between">
                <h3><strong>{this.state.bookInfo.title}</strong></h3>
            </div>
            <br />
            <table className="table wtable-info">
                <tbody>
                    <tr><td>ID</td><td>{bookInfo.id}</td></tr>
                    <tr><td>Tn sch</td><td>{bookInfo.title}</td></tr>
                    <tr>
                        <td>Tc gi</td>
                        <td className="split-string">{bookInfo.authors?.map((author) => <Link
                            to={`/admin/author/detail?id=${author.id}`}
                            children={author.name}
                            key={author.authorCode} />)}</td>
                    </tr>
                    <tr>
                        <td>Th loi</td>
                        <td className="split-string">{bookInfo.categories?.map((category) => <Link
                            to={`/admin/category/detail?id=${category.id}`}
                            children={category.category}
                            key={category.categoryCode} />)}</td>
                    </tr>
                    <tr><td>Nh xut bn</td><td><Link to={`/admin/publisher/detail?id=${bookInfo.id}`} children={bookInfo.publisher?.name} /></td></tr>
                    <tr><td>S lng</td><td>{bookInfo.quantity}</td></tr>
                    <tr><td>Ti bn</td><td>{bookInfo.edition}</td></tr>
                    <tr><td>S trang</td><td>{bookInfo.pageNumber}</td></tr>
                    <tr><td>im nh gi</td><td>{bookInfo.rate}</td></tr>
                    <tr><td>S lt nh gi</td><td>{bookInfo.rateCount}</td></tr>
                    <tr><td>M t</td><td>{bookInfo.description}</td></tr>
                    <tr>
                        <td>Cp nht</td>
                        <td><Link to={`/admin/admin-user/detail?adminCode=${bookInfo.modifiedBy}`}>{bookInfo.modifiedBy}</Link> - {moment(bookInfo.modifiedDate).calendar()}</td>
                    </tr>
                    <tr>
                        <td>To bi</td>
                        <td><Link to={`/admin/admin-user/detail?adminCode=${bookInfo.createBy}`}>{bookInfo.createBy}</Link> - {moment(bookInfo.createDate).calendar()}</td>
                    </tr>
                </tbody>
            </table>
        </div>)
        return <DetailAdminPage ref={this.detailPage}
            interceptor={(menu) => !this.state.isNotFoundResult
                ? menu
                : (<div className="d-flex space-sm">
                    <Button color="primary" size="sm" onClick={this.getBook.bind(this)}><ReloadSVG color="white" className="icon" /> Ti li</Button>
                </div>)
            }
            goBackAction={goBackAction}
            menuConfig={[
                {
                    name: 'Xem thng tin',
                    body: viewBook,
                    callback: () => {
                        if (this.nextUpdate) {
                            this.nextUpdate = false;
                            this.getBook();
                        }
                    }
                },
                {
                    name: 'Cp nht',
                    body: <UpdateBook
                        bookInfo={this.state.bookInfo}
                        getAdminMessageRequest={this.getAdminMessageReq.bind(this)}
                        onUpdateSuccess={() => this.nextUpdate = true}
                    />
                }
            ]}
        />
    }
    async getBook() {
        const goBackId = this.props.goBack && this.props.goBack.bookId
        const requestId = new URLSearchParams(window.location.search).get("id");
        const id = goBackId || requestId;
        const adminMessageRequest = this.getAdminMessageReq();
        console.log('get book again')
        if (id) {
            if (this.props.goBack) window.history.pushState('', '', `/admin/book/detail?id=${id}`)

            const resultReq = await adminMessageRequest.sendRequest(() => {
                return BookAPI.getBook(id as number)
            }, { hideWhenDone: true });

            if (resultReq) {
                this.setState({ bookInfo: resultReq.data });
                return;
            }
        }
        this.setState({ isNotFoundResult: true });
    }
}