import React from "react";
import { Link } from "react-router-dom";
import { Button, Col, Container, Row } from "reactstrap";
import AuthorAPI, { AuthorAPIResult } from "../../../../api/admin/author-api";
import CategoryAPI, { CategoryAPIResult } from "../../../../api/admin/category-book-api";
import WForm from "../../../../components/wform";
import WInput, { WInputOther } from "../../../../components/winput";
import AMRWindow from "../../../../page-component/admin/admin-message-request-disable-window";

const goBackURL = "/admin/book/author/view"
const mainURL = "/admin/book/author/add"

export default class AddAuthor extends React.Component {
    amr = React.createRef<AMRWindow>();
    form = React.createRef<WForm>();
    componentDidMount() {
        const form = this.form.current
        if (!form) return
        form.setData({
            authorCode: "NGOC_CAM",
            name: "Ngc Cam",
            description: "My name is Ngoc Cam"
        } as AuthorAPIResult)
    }
    insert(evt: any) {
        evt.preventDefault()
        const { form: { current: form }, amr: { current: amr } } = this
        if (!form || !amr) return
        const sendRequest = amr.getMessageRequest().sendRequest

        const data: AuthorAPIResult = form.getData()
        sendRequest(
            AuthorAPI.addAuthor(data))
    }
    render() {
        return <AMRWindow ref={this.amr} className="form-container">
            <WForm ref={this.form} onSubmit={this.insert.bind(this)} >
                <h3>Thm tc gi</h3>
                <Container fluid={true} className="p-0">
                    <Row>
                        <Col xs={12}>
                            <WInput title_input="M tc gi" name="authorCode" />
                        </Col>
                        <Col xs={12}>
                            <WInput title_input="Tn tc gi" name="name" />
                        </Col>
                        <Col>
                            <WInputOther title_input="M t">
                                <textarea rows={4} name="description" />
                            </WInputOther>
                        </Col>
                    </Row>
                    <div className="d-flex space-sm mt-2">
                        <Button size="sm" color="primary">Thm</Button>
                        <Link to={goBackURL} className="btn btn-sm btn-danger">Quay v</Link>
                    </div>
                </Container>
            </WForm>
        </AMRWindow>
    }
}