import React from "react";

export const AdminOverrideWindow = React.createContext<AdminOverrideWindowState>(undefined as any);
interface AdminOverrideWindowState {
    addWindow: (window: JSX.Element) => JSX.Element,
    removeWindow: (window: JSX.Element) => void,
    clear: () => void,
    getWindows: () => JSX.Element[]
}
export default class AdminOverrideWindowProvider extends React.Component<{}, AdminOverrideWindowState> {
    windows: JSX.Element[] = [];
    constructor(props: any) {
        super(props);
        this.state = {
            addWindow: (window) => {
                const index = this.windows.indexOf(window)
                if (index !== -1) return null as any
                this.windows.push(window)
                this.setState({})
                return window;
            },
            removeWindow: (window) => {
                const { windows } = this
                let index = windows.indexOf(window)
                if (index !== -1) windows[index] = null as any
                let indexClear = windows.length
                while (true) {
                    if (windows[--indexClear] !== null as any) break;
                }
                this.windows = windows.slice(0, indexClear + 1)
                this.setState({})
            },
            clear: () => { this.windows = []; this.setState({}) },
            getWindows: () => [... this.windows]
        }
    }
    render() {
        return (
            <AdminOverrideWindow.Provider
                value={{ ... this.state, ...this.windows }}>
                {this.props.children}
            </AdminOverrideWindow.Provider>
        )
    }
}