import React from "react";
import { Route, Switch } from "react-router-dom";
import ViewCategoryBook from "../pages/admin/book/category/view";
import ViewBook from "../pages/admin/book/view-book";
import ViewPermission from "../pages/admin/permission/view-permission";
export class ac extends React.Component { }

const Utils = {
    formToJson: (formData: FormData) => {
        let result: any = {};
        for (let [name, value] of (formData.entries() as any)) {
            if (result.hasOwnProperty(name)) {
                if (result[name] instanceof Array) {
                    result[name].push(value);
                } else {
                    result[name] = [result[name], value];
                }
            } else {
                result[name] = value;
            }
        }
        return result;
    },
    viewImageFromInputFileE: (inputTarget: HTMLInputElement, viewTarget: HTMLImageElement) => {
        const file = inputTarget.files && inputTarget.files[0]

        if (!file) return
        let reader = new FileReader();
        reader.onload = ({ target }) => {
            viewTarget.src = target?.result as any;
        };
        reader.readAsDataURL(file);

        return file
    },
    modelListToObject: (list: any[], idField: string = 'id') => {
        return list.reduce((acc: any, cur: any) => ({ ...acc, [cur[idField]]: cur }), {})
    },
    lostFocus: () => {
        $('#lost-focus').focus();
    },
    waitPromise: (time: number) => new Promise((resolve) => setTimeout(resolve, time)),
    preventSetStateWhenComponentUnmount: (component: any) => {
        const setState = component.setState
        const unmount = component.componentWillUnmount
        component.__isMounted = true
        component.componentWillUnmount = function () {
            this.__isMounted = false
            console.log(this.prototype)
            unmount && unmount()
        }
        component.setState = function (...args: any) {
            if (this.__isMounted) setState.apply(this, args)
        }
    },
    getPageRequest: (pageDefault: { page: number, limit: number } = { page: 1, limit: 10 }) => {
        let search = new window.URLSearchParams(window.location.search as any);
        let page: number = Number.parseInt(search.get('page') as any)
        if (!Number.isInteger(page)) page = pageDefault.page;
        let limit: number = Number.parseInt(search.get('limit') as any)
        if (!Number.isInteger(limit)) limit = pageDefault.limit;
        return { page: page, limit: limit }
    },
    mapValue: (() => {
        let vnMap = { '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'i', '': 'i', '': 'i', '': 'i', '': 'i', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'y', '': 'y', '': 'y', '': 'y', '': 'y' };
        let mapLowCase: any = {};
        let specialCharacter = { '_': ' ', '-': ' ' };
        for (let i = 97; i < 123; i++) {
            mapLowCase[String.fromCharCode(i - 32)] = String.fromCharCode(i);
        }
        return { ...vnMap, ...mapLowCase, ...specialCharacter };
    })(),
    filterString: (value: string, getText: (value: any) => string) => {
        // Chuyn d liu  khng phi so snh 2 ln
        let textArray = value.toLowerCase().split('').map((value) => {
            let newVal = Utils.mapValue[value];
            if (newVal) { return newVal; }
            return value;
        });
        let length = value.length;

        return function (data: any) {
            let countIndex = 0;
            let nextChar = textArray[countIndex];
            for (let char of getText(data)) {
                if (countIndex !== length) {
                    if (nextChar === char || Utils.mapValue[char] === nextChar) {
                        nextChar = textArray[++countIndex];
                    }
                } else {
                    break;
                }
            }
            return countIndex === length;
        }
    },
    randomString: (length: number = 10): string => {
        let result = '';
        let characters = 'abcdefghijklmnopqrstuvwxyz';
        let charactersLength = characters.length;
        for (let i = 0; i < length; i++) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    },
    nextClick: (callback: () => any) => {
        document.addEventListener('click', getEventClickOneTime())
        function getEventClickOneTime() {
            const evt = () => {
                document.removeEventListener('click', evt)
                callback()
            }
            return evt;
        }
    },
    renderRoute: (configRoute: RouteRender) => {
        const childs = []
        for (let path in configRoute) {
            childs.push(renderSwitch(configRoute[path], '/' + path.replace(/\./g, '/')))
        }
        return React.createElement(Switch, null, childs)

        function renderSwitch(config: RouteRender, parentPath: string): any {
            const output = []
            const routes = []
            const getPath = (parent: string, path: string) => `${parent}/${path.replace(/\./g, '/')}`
            const { __component, __isExact, __children } = config
            let parentRoute
            if (__component || __children) {
                parentRoute = React.createElement(Route, {
                    path: parentPath,
                    component: __component as any,
                    exact: __isExact as any,
                    children: __children,
                    key: parentPath
                })

                if (__isExact)
                    return parentRoute
            }

            for (let path in config) {
                if (path === '__component' || path === '__isExact' || path === '__children') continue
                routes.push(renderSwitch(config[path], getPath(parentPath, path)))
            }

            if (routes.length === 0) {
                if (!parentRoute) return null
                return parentRoute
            }

            if (parentRoute) routes.push(parentRoute)

            output.push(
                React.createElement(Route,
                    {
                        path: parentPath,
                        children: React.createElement(Switch, null, routes)
                    }))
            return output

        }
    },
    setToArray: function <K>(data: Set<K>): K[] {
        let result = new Array(data.size);
        let index = 0;
        for (let value of data as any) {
            result[index++] = value;
        }
        return result;
    },
}
export default Utils;
export interface RouteRender {
    [path: string]: RouteRenderChild
}
interface RouteRenderChild {
    __component?: React.ComponentType<any>
    __children?: JSX.Element
    __isExact?: any
    [path: string]: RouteRender | any
}

