import React from 'react';
import { Redirect } from 'react-router-dom';
import Utils from '../../common/Utils';
import AdminAuthenticationProvider, { AdminAuthentication } from '../../contexts/admin-authencation';
import AdminOverrideWindowProvider, { AdminOverrideWindow } from '../../contexts/admin-overide-window';
import AdminHeader from '../../page-component/admin/admin-header';
import MenuTab from '../../page-component/admin/menu-tab';
import './admin-page.scss';
import AddAdminUser from './admin-user/add-user';
import ViewAdminUser from './admin-user/admin-users-view';
import DetailAdminUser from './admin-user/detail-user';
import AddBook from './book/add-book';
import AddAuthor from './book/author/add';
import ViewAuthorBook from './book/author/view';
import AddCategory from './book/category/add';
import DetailCategory from './book/category/detail';
import ViewCategoryBook from './book/category/view';
import DetailBook from './book/detail-book';
import AddPublisher from './book/publisher/add';
import DetailPublisher from './book/publisher/detail';
import ViewPublisherBook from './book/publisher/view';
import ViewBook from './book/view-book';
import ViewGroupPermission from './group-permission/view-group-permission';
import AddPermission from './permission/add-permission';
import DetailPermission from './permission/detail-permission';
import ViewPermission from './permission/view-permission';

const menu = [
    {
        content: 'Ti khon qun tr', items: [
            { content: 'Ti khon', hrefLink: '/admin/admin-user/view' },
            { content: 'Chc nng', hrefLink: '/admin/permission/view' },
            { content: 'Nhm chc nng', hrefLink: '/admin/group/view' }
        ]
    }, {
        content: 'Qun l sch', items: [
            { content: 'Sch', hrefLink: '/admin/book/view' },
            { content: 'Nh xut bn', hrefLink: '/admin/book/publisher/view' },
            { content: 'Tc gi', hrefLink: '/admin/book/author/view' },
            { content: 'Th loi', hrefLink: '/admin/book/category/view' },
        ]
    }
]


export const configRoute = {
    admin: {
        __children: <Redirect to="/admin/admin-user/view" />,
        book: {
            view: { __component: ViewBook, __isExact: true },
            add: { __component: AddBook, __isExact: true },
            detail: { __component: DetailBook, __isExact: true },

            category: {
                view: { __component: ViewCategoryBook, __isExact: true },
                add: { __component: AddCategory, __isExact: true },
                detail: { __component: DetailCategory, __isExact: true },
            },
            publisher: {
                view: { __component: ViewPublisherBook, __isExact: true },
                add: { __component: AddPublisher, __isExact: true },
                detail: { __component: DetailPublisher, __isExact: true },
            },
            author: {
                view: { __component: ViewAuthorBook, __isExact: true },
                add: { __component: AddAuthor, __isExact: true },
            }
        },
        'admin-user': {
            view: { __component: ViewAdminUser, __isExact: true },
            detail: { __component: DetailAdminUser, __isExact: true },
            add: { __component: AddAdminUser, __isExact: true },
        },
        permission: {
            view: { __component: ViewPermission, __isExact: true },
            detail: { __component: DetailPermission, __isExact: true },
            add: { __component: AddPermission, __isExact: true },
        },
        group: {
            view: { __component: ViewGroupPermission, __isExact: true },
            detail: { __component: DetailPermission, __isExact: true },
            add: { __component: AddPermission, __isExact: true },
        }
    }
}

const routeComponent = Utils.renderRoute(configRoute)

export default class AdminPage extends React.Component<{}, { isHideScroll: boolean }> {
    isClearWindow = true;
    adminHeader = React.createRef<AdminHeader>();

    constructor(props: any) {
        super(props);
        this.state = { isHideScroll: false };
    }

    render() {
        this.isClearWindow = true;
        return (
            <AdminAuthenticationProvider>
                <AdminOverrideWindowProvider>
                    <AdminAuthentication.Consumer>
                        {({ isLogin }) => {
                            return isLogin
                                ? <div id="parent-layout">
                                    <style children="html{background-color: #eee}" />
                                    <AdminHeader minScreenResizeMenuTab={500} ref={this.adminHeader} />
                                    <div id='app-body' style={{ height: 'calc(100% - 35px)', width: '100%', position: 'relative' }}
                                        className={`d-inline-flex align-items-start`}>
                                        <MenuTab menu={menu} getAdminHeader={() => { return this.adminHeader.current as any }}></MenuTab>
                                        <div id='app-content'>
                                            {routeComponent}
                                        </div>
                                        <AdminOverrideWindow.Consumer>
                                            {({ getWindows, clear }) => {
                                                const windows = getWindows()
                                                if (this.isClearWindow) {
                                                    if (windows.length > 0) clear();
                                                    this.isClearWindow = false;
                                                    return null;
                                                }

                                                if (windows.length > 0) {
                                                    $('#app-content').addClass('d-none');
                                                    const lastIndex = windows.length - 1
                                                    return (
                                                        <div className="override-content">
                                                            {windows.map((window, index) => <div key={index} className={index !== lastIndex ? 'd-none' : ''}>{window}</div>)}
                                                        </div>
                                                    )
                                                }

                                                $('#app-content').removeClass('d-none');
                                            }}
                                        </AdminOverrideWindow.Consumer>
                                    </div>
                                </div >
                                : <Redirect to="/admin/login"></Redirect>
                        }}
                    </AdminAuthentication.Consumer>
                </AdminOverrideWindowProvider>
            </AdminAuthenticationProvider >
        )
    }
}