import moment from "moment";
import React, { Fragment } from "react";
import { Button } from "reactstrap";
import PublisherAPI, { PublisherAPIResult } from "../../../../api/admin/publisher-api";
import { APIResult } from "../../../../api/declare";
import { AdminOverrideWindow } from "../../../../contexts/admin-overide-window";
import EditSVG from "../../../../logo-svg/edit";
import BaseCrudView, { BaseViewConfig } from "../../base/base-view";
import DetailPublisher from "./detail";

export default class ViewPublisherBook extends BaseCrudView<PublisherAPIResult>{
    getId(data: PublisherAPIResult) {
        return data.id
    }
    fillData(data: PublisherAPIResult, unselectClass: string): JSX.Element {
        return <Fragment>
            <td>{data.name}</td>
            <td>{data.publisherCode}</td>
            <td title={`Cp nht: ${data.modifiedBy} - ${moment(data.modifiedDate).calendar()}\nKhi to: ${data.createBy} - ${moment(data.createDate).calendar()}`
            }>{data.modifiedBy}</td>
            <td className="text-center">
                <AdminOverrideWindow.Consumer>
                    {({ addWindow, removeWindow }) => {
                        return <Button color="primary" size="sm" className={unselectClass} onClick={() => {
                            const detailPage = addWindow(<DetailPublisher
                                goBack={{
                                    action: () => removeWindow(detailPage),
                                    id: data.id
                                }}
                            />)
                        }}>
                            <EditSVG color="white" className="icon" />
                        </Button>
                    }}
                </AdminOverrideWindow.Consumer></td>
        </Fragment >
    }
    getPageAPIMethod(page: number, limit: number): Promise<APIResult<any, any>> {
        return PublisherAPI.getAll({ page: page, limit: limit, unlimited: false })
    }
    subRender(): JSX.Element | null {
        return null
    }
    configure(): BaseViewConfig<PublisherAPIResult> {
        return {
            headers: [
                'M nh xut bn',
                'Tn nh xut bn',
                'Cp nht',
                <div className="text-center">Chi tit</div>
            ],
            urlPage: '/admin/book/publisher/view',
            addPage: {
                textButton: 'Thm nh xut bn',
                link: '/admin/book/publisher/add',
            },
            deleteAction: {
                textButton: 'Xa', deleteMethod: PublisherAPI.deletePublishers
            }
        }
    }

}